h2,h3{
    display:inline;
}
.Wrapper{
    display: flex;
    flex-direction: column;
    /*border: 1px solid #ddd;*/
    flex:1;
    margin-right: 30px;
    margin-left: -50px;
}
.cv {
    text-decoration: none;
    font-size: 25px;
    color: rgba(15, 15, 15, 0.97);
}

.News{
    width: 120%;
    text-align: left;
    margin-top: 50px;
}
.icon-links{
    display: flex;
    justify-content: center;
    gap: 20px; /* 调整元素之间的间隔 */
    color: black;
}
.photo {
    width: 70%;
    padding: 60px;
    text-align: center;
}
.photo img {
    max-width: 220px;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.intro {
    width: 64%;
    padding: 10px;
    text-align: left;
    flex:2;
}
.intro h1 {
    margin-top: 0;
}
.projects-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    text-align: center;
}
.projects-container img,video {
    width:380px;
    height:212px;
    transform: scale(1, 1.13);
    margin-bottom: -2px;
}

.projects-container a:hover {
    font-weight: bold;
}

@media screen and (min-width: 700px) {
    .whole{
        /*font-family:  "Gill Sans MT", sans-serif;*/
        /*font-size: 16px;*/
        font-family: "Dosis", sans-serif;
        font-optical-sizing: auto;
        font-weight: 470;
        font-style: normal;

        width: 100%; /* 设置容器宽度为100% */
        justify-content: center; /* 在容器内水平居中 */
    }
    .container {
        display: flex;
        max-width: 1200px; /* 设置内容的最大宽度 */
        width: 100%; /* 设置内容宽度为100% */
        margin: 0 auto;
    }
}

@media screen and (max-width: 700px) {
    .whole {
        /* 在显示器宽度小于600像素时，修改样式 */
        max-width: 90%; /* 最大宽度改为100% */
        font-family: "Handjet", sans-serif;
        font-size: 16px;
        justify-content: center; /* 在容器内水平居中 */
    }
    .container {
        flex-direction: column;
    }
    .photo{
        flex: none;
        width: 80%;
    }
    .intro{
        width: 105%;
    }
}
.subintro a {
    text-decoration: underline;
    text-decoration-color: #48D1CC;
    text-decoration-style: solid;
    text-decoration-thickness: 2px; /* 根据需要调整下划线的粗细 */
}
.subintro a:hover {
    background-color: #48D1CC;
}
a {
    color: black;
}
.fa-twitter:hover {
    color: #48D1CC;
}
.fa-github:hover {
    color: #48D1CC;
}
.fa-envelope:hover {
    color: #48D1CC;
}
.cv:hover {
    color: #48D1CC;
}
footer {
    text-align: center;
}
*::selection {
    background:#48D1CC;
}
img {
    user-select: none;
}


